.header {
  background-image: url(../images/header-bg.png);
  background-size: cover;
  background-position: center;



  &__content {
    height: 100vh;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  &__title {
      margin: 0;
      font-size: 36px;
      font-weight: 700;
      line-height: 36px;
      text-align: left;

    @include on-tablet {
        font-size: 48px;
      }
    }

    &__bottom {
      padding-bottom: 40px;


      @include on-tablet {
        padding-bottom: 65px;
      }
    }
}


